    #icons-wrapper ul::after {
        clear: both;
        content: "";
        display: table;
    }

    #icons-wrapper ul li {
        width: 25%;
        float: left;
        padding: 16px 0;
        text-align: center;
        border-radius: .25em;
        -webkit-transition: background 0.2s;
        -moz-transition: background 0.2s;
        transition: background 0.2s;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        overflow: hidden;
    }

    #icons-wrapper ul li:hover {
        background: #dbdbdb;
    }

    #icons-wrapper ul p,
    #icons-wrapper ul em,
    #icons-wrapper ul input {
        display: inline-block;
        font-size: 1rem;
        color: #999999;
        -webkit-user-select: auto;
        -moz-user-select: auto;
        -ms-user-select: auto;
        user-select: auto;
        white-space: nowrap;
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        cursor: pointer;
    }

    #icons-wrapper ul p {
        padding: 10px;
        margin-top: 10px;
    }

    #icons-wrapper ul p::selection,
    #icons-wrapper ul em::selection {
        background: #766acf;
        color: #efefef;
    }

    #icons-wrapper ul em {
        margin-bottom: 8px;
    }

    #icons-wrapper ul em::before {
        content: '[';
    }

    #icons-wrapper ul em::after {
        content: ']';
    }

    #icons-wrapper ul input {
        text-align: center;
        background: transparent;
        border: none;
        box-shadow: none;
        outline: none;
    }

    @media only screen and (min-width: 768px) {
        #icons-wrapper ul li {
            width: 20%;
            float: left;
        }
    }

    @media only screen and (min-width: 1024px) {
        #icons-wrapper ul li {
            width: 12.5%;
            float: left;
            padding: 32px 0;
        }
    }

